.content-form-contact{
    background-color:rgba(11, 11, 12, 0.5) ;
    width: 40%;
    padding:40px 20px 20px 20px;
    border-radius: 10%;
    display: flex;
    justify-content: center;
    gap: 10%;
    margin-bottom: 20px;
}
.form-contact{
    padding-left: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    
}
form input{
    height: 30px;
    width: 250px;
}
form textarea{
    height: 150px;
    width: 250px;
}
.title-contact{
    color: white;
    font-size: 56px;
    font-weight: 600;
}
.text-contact{
    font-size:22px ;
    padding-bottom: 20px;
}
.container-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: linear-gradient(
        to bottom,
        rgba(12, 12, 12, 0.5),
        rgba(119, 119, 123, 0.5)
        ),url(../assets/font-contact.webp ) ;
        background-size: cover;
        background-position: center;
}
.info-contact i{
    color: white;
    font-size: 150%;
}
.info-contact{
    padding-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.text-info-contact{
    padding-bottom: 5px;
    text-align: center;
}
.btn-message{
    display: inline-block;
    margin-top: 10px;
    padding: 10px 28px ;
    background:  rgb(4, 83, 89);
    border-radius: 40px;
    font-size: 18px;
    color: white;
    letter-spacing: 1px;
    font-weight: 500;
    cursor: pointer;
}

.btn-message:hover{
    box-shadow: 0 0 5px rgb(21, 143, 143),
    0 0 5px rgb(21, 143, 143), 0 0 10px rgb(21, 143, 143),
    0 0 20px rgb(21, 143, 143), 0 0 30px rgb(21, 143, 143);
}

.icone-form{
    background-color: forestgreen;
    border-radius: 50%;
    padding: 10px;
    margin-bottom: 10px;    
}

.message-confirm{
    display: block;
    background-color: rgba(24, 166, 50, 0.526);
}
/* RESPONSIVE CSS */
@media screen and (max-width: 1024px) {
    .content-form-contact{
        padding: 30px;
        margin-top: 50px;
        gap: 5%;
    }
    
    .form-contact{
        padding-left: 0;
    }
    form input{
        height: 30px;
        width: 200px;
    }
    form textarea{
        height: 120px;
        width: 200px;
    }
    .title-contact{
        font-size: 36px;
        margin-top: 50px;
    }
    .text-contact{
        font-size:16px ;
        padding-bottom: 30px;
    }
   
    .info-contact i{
        font-size: 120%;
    }
    .info-contact{
        padding-top: 100px;
    }
}

/* RESPONSIVE CSS */
@media screen and (max-width: 680px) {
    .content-form-contact{
        flex-direction: column;
        align-items: center;
        width: 70%;
        border-radius: 5%;
        padding-bottom: 0;
        margin-bottom: 15px;
        margin-top: 0;
    }
    
    .title-contact{
        font-size: 36px;
        margin-top: 0;
    }
    .text-contact{
        font-size:14px ;
        width: 70%;
        font-weight: 500;
        text-align: center;
    }
    form input{
        width: 250px;
    }
    form textarea{
        height: 230px;
        width: 250px;
    }
   
    .info-contact{
        padding-top: 20px;
       
    }
    .text-form{
        font-size: 14px;
    }
    
}
